import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAirbnb } from "@fortawesome/free-brands-svg-icons";

const navItem = (props) => {
  return (
    <li key={props.name}>
      <a href={props.href}>
        <div class='items'>
          <FontAwesomeIcon icon={faAirbnb} /> {props.name}
        </div>
      </a>
    </li>
  );
};

export default navItem;
