import React, { Component } from "react";
import NavItem from "./NavItem";

const NAV_ITEM = ["About me", "Projects", "Blogs"];

export default class NavItemsList extends Component {
  render() {
    return (
      <ul className='list-unstyled components'>
        {NAV_ITEM.map((element) => (
          <NavItem name={element} />
        ))}
      </ul>
    );
  }
}
