import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default function socialIconItem(props) {
  return (
    <a href={props.url} rel='noopener' target='_blank' aria-label='ss'>
      <FontAwesomeIcon className='social-icon' icon={props.iconType} />
    </a>
  );
}
